<template>
  <v-app>
    <v-app-bar app color="#00ACC1">
      <v-app-bar-nav-icon
        v-if="windowWidth < 800"
        @click="openDrawer"
      ></v-app-bar-nav-icon>
      <v-app-bar-title class="font-weight-black white--text">
        智慧教育 升學規劃
      </v-app-bar-title>
      <v-spacer></v-spacer>
      <v-btn-toggle mandatory group color="white" v-model="activeButton">
        <v-btn
          width="65px"
          class="font-weight-black white--text"
          @click="changeSchoolSystem('senior')"
          href="#department"
          >高中</v-btn
        >
        <v-btn
          width="65px"
          class="font-weight-black white--text"
          @click="changeSchoolSystem('vocational')"
          href="#department"
          >高職</v-btn
        >
        <v-btn
          width="65px"
          class="font-weight-black white--text"
          @click="changeSchoolSystem('five_year')"
          href="#department"
          >五專</v-btn
        >
      </v-btn-toggle>
    </v-app-bar>
    <v-main v-resize="resize">
      <v-container fluid>
        <schoolSystemIntro @changeActiveBtn="changeActiveBtn" />
        <departmentIntro :windowWidth="windowWidth" />
      </v-container>
    </v-main>
  </v-app>
</template>

<script>
import schoolSystemIntro from "@/components/schoolSystemIntro";
import departmentIntro from "@/components/departmentIntro";

export default {
  name: "App",
  components: {
    departmentIntro,
    schoolSystemIntro,
  },
  data: () => ({
    activeButton: 0,
    windowWidth: "",
  }),
  methods: {
    changeSchoolSystem(schoolSystem) {
      this.$store.commit("changeSchoolSystem", schoolSystem);
    },
    changeActiveBtn(index) {
      this.activeButton = index;
    },
    resize() {
      this.windowWidth = window.innerWidth;
    },
    openDrawer() {
      this.$bus.$emit("openDrawer");
    },
  },
  mounted() {
    this.resize();
  },
};
</script>

